import { TileColor } from './mahjong_tile'

interface NumberdTile {
  color: TileColor
  maxNumber: number
}

interface SymboledTile {
  color: TileColor
  number: number
}

interface TileMap {
  numberdTileMap: {[name:string]:NumberdTile}
  symboledTileMap: {[name:string]:SymboledTile}
  option: {[name:string]:string}
}

