export class _ {
  /**
   *  true  true 
   * @param f 
   * @param arr 
   * @returns 
   */
  static everyPairFromHeadWith<T> (f:(current:T, next:T) => boolean, arr:T[]):boolean {
    const pairs = _.makePairsFromHead(arr)
    return pairs.every((p) => f(p[0], p[1]))
  }

  /**
   * 
   *  true  true 
   * 
   * @param f 
   * @param arr 
   * @returns 
   */
  static everyPairAllPairWith<T> (f:(a:T, b:T) => boolean, arr:T[]):boolean {
    const pairs = _.makeAllPairsFromHead(arr)
    return pairs.every((p) => f(p[0], p[1]))
  }

  /**
   * 
   * [1, 2, 3, 4] -> [ [ 1, 2 ], [ 1, 3 ], [ 1, 4 ], [ 2, 3 ], [ 2, 4 ], [ 3, 4 ] ]
   * @param arr 
   * @returns 
   */
  static makeAllPairsFromHead<T> (arr:T[]):[T, T][] {
    if (arr.length <= 1) {
      return []
    }
    const pairs: [T, T][] = []
    for (let indexA = 0; indexA < arr.length - 1; indexA++) {
      for (let indexB = indexA + 1; indexB < arr.length; indexB++) {
        pairs.push([arr[indexA], arr[indexB]])
      }
    }
    return pairs
  }

  /**
   * 2[1, 2, 3] -> [[1, 2], [2, 3]]
   * @param arr 
   * @returns 
   */
  static makePairsFromHead<T> (arr:T[]):[T, T][] {
    const pairs: [T, T][] = []
    for (let index = 0; index < arr.length - 1; index++) {
      pairs.push([arr[index], arr[index + 1]])
    }
    return pairs
  }

  /**
   * 2
   * @param arrT 
   * @param arrS 
   * @returns 
   */
  static pairOf<T, S> (arrT:T[], arrS:S[]):[T, S][] {
    const pairs:[T, S][] = []
    arrT.forEach((t) => {
      arrS.forEach((s) => {
        pairs.push([t, s])
      })
    })
    return pairs
  }

  /**
   *  [, ] 
   * @param arr 
   * @returns 
   */
  static extractAllOne<T> (arr:T[]):[T, T[]][] {
    return arr.map((v, i, arr) => {
      const _arr:T[] = arr.concat()
      _arr.splice(i, 1)
      return [v, _arr]
    })
  }

  /**
   *  true 
   * @param arrA 
   * @param arrB 
   * @param compare ( @see Array.prototype.sort() )
   * @returns 
   */
  static equalArray<T> (arrA:T[], arrB:T[], compare:(a:T, b:T) => number):boolean {
    if (arrA.length !== arrB.length) {
      return false
    }
    let ret = true
    arrA.forEach((a, i) => {
      ret = ret && (compare(a, arrB[i]) === 0)
    })
    return ret
  }

  /**
   * 
   * equalArray(as.sort(compare), bs.sort(compare), compare) 
   * @param arrA 
   * @param arrB 
   * @param compare ( @see Array.prototype.sort() )
   * @returns 
   */
  static equalSetArray<T> (arrA:T[], arrB:T[], compare:(a:T, b:T) => number):boolean {
    const _arrA = [...arrA].sort(compare)
    const _arrB = [...arrB].sort(compare)
    return this.equalArray(_arrA, _arrB, compare)
  }

  /**
   * 
   * @param arrA A
   * @param arrB B
   * @param compare  ( @see Array.prototype.sort() )
   * @returns A 
   */
  static compareArray<T> (arrA:T[], arrB:T[], compare:(a:T, b:T) => number):number {
    for (let i = 0; i < arrA.length; i++) {
      if (i >= arrB.length) {
        return 1
      }
      const compared = compare(arrA[i], arrB[i])
      if (compared !== 0) {
        return compared
      }
    }
    if (arrA.length < arrB.length) {
      return -1
    }
    return 0
  }

  /**
   *  n 
   *  compare  
   * @param arr 
   * @param n 
   * @param compare 
   * @returns [[][]]
   */
  static extractAllN<T> (
    arr:T[],
    n:number,
    compare:(a:T, b:T) => number
  ):[T[], T[]][] {
    return this._extractAllN(arr, n, compare, 0)
  }

  /**
   * extractAllN  
   * @param arr 
   * @param n 
   * @param compare 
   * @param startIndex: 
   */
  static _extractAllN<T> (
    arr:T[],
    n:number,
    compare:(a:T, b:T) => number,
    startIndex:number
  ):[T[], T[]][] {
    if (n === 0) {
      return [[[], [...arr]]]
    }
    const ret:[T[], T[]][] = []
    for (let index = startIndex; index <= arr.length - n; index++) {
      const _arr = [...arr]
      const takenIs = _arr.splice(index, 1)
      const takenP = this._extractAllN([..._arr], n - 1, compare, index)
      takenP.forEach(([taken, remaingArr]) => {
        const _taken:T[] = takenIs.concat(taken)
        if (ret.every(
          ([retTaken, remain]) => !(_.compareArray(retTaken.sort(compare), _taken.sort(compare), compare) === 0)
        )) {
          ret.push([_taken, remaingArr])
        }
      })
    }
    return ret
  }

  /**
   * 
   * @param arr 
   * @returns 1
   */
  static formation<T> (arr:T[][]):T[][] {
    const _arr:T[][] = []
    arr.forEach((p) => {
      _arr.push([...p])
    })
    const firstMaterials:T[][] = (_arr.shift() || []).map(m => [m])
    return this._formation(firstMaterials, _arr)
  }

  /**
   * formation 
   * @param conbined 
   * @param arr 
   */
  private static _formation<T> (conbined:T[][], arr:T[][]):T[][] {
    if (arr.length === 0) {
      return conbined
    }
    const _retConbined:T[][] = []
    const arrHead:T[] = arr.shift() || []
    conbined.forEach(cs => {
      arrHead.forEach(hm => {
        const _cs:T[] = [...cs]
        _cs.push(hm)
        _retConbined.push(_cs)
      })
    })
    return this._formation(_retConbined, arr)
  }

  /**
   * arr  equal  m 
   * m 
   * arr 
   * @param arr 
   * @param m 
   * @param equal 
   * @returns  arr
   */
  static extract<T> (arr:T[], m:T, equal:(a:T, b:T) => boolean):T[] {
    const _arr = [...arr]
    const index = _arr.findIndex((f) => equal(f, m))
    if (index === -1) {
      return _arr
    }
    _arr.splice(index, 1)
    return _arr
  }

  /**
   * arr  equal  ms 
   * ms 
   * arr 
   * @param arr 
   * @param ms 
   * @param equal 
   * @returns  arr
   */
  static extractMultiple<T> (arr:T[], ms:T[], equal:(a:T, b:T) => boolean):T[] {
    let _arr = [...arr]
    ms.forEach(m => {
      _arr = this.extract(_arr, m, equal)
    })
    return _arr
  }

  /**
   * 
   * @param arr 
   * @returns 
   */
  static shuffleArr<T> (arr:T[]):T[] {
    const _arr = [...arr]
    let out:T[] = []
    for (let index = _arr.length; index >= 0; index--) {
      const randomIndex = Math.floor(Math.random() * (index + 1))
      const s = _arr.splice(randomIndex, 1)
      out = out.concat(s)
    }
    return out
  }

  /**
   * 
   * @param arr 
   * @param a 
   * @param equal 
   * @returns 
   */
  static isContained<T> (arr:T[], a:T, equal:(arg1:T, arg2:T) => boolean):boolean {
    return (!(arr.findIndex(x => equal(x, a)) === -1))
  }

  /**
   * 
   * @param arr 
   * @param compare 
   * @returns 
   */
  static uniq<T> (arr:T[], compare:(ta:T, tb:T) => number):T[] {
    const ret = [...arr]
    ret.sort(compare)
    for (let index = 0; index < ret.length - 1;) {
      if (compare(ret[index], ret[index + 1]) === 0) {
        ret.splice(index, 1)
      } else {
        index++
      }
    }
    return ret
  }
}
